<template lang="pug">
  div
    h2.page-subtitle 
    div.container
      div.card
        div.card-body
          p(style={'font-size':'20px'}) {{ ruleVersion | substring(0,8) }} #[a(href='https://github.com/HackerSir/Articles-of-association' target='_blank') ] 
      div.card
        div.card-body.text-left
          vue-markdown(:source="rule")
</template>

<script>
import VueMarkdown from 'vue-markdown'
export default {
  data: function () {
    return {
      //  commit 
      ruleVersion: '',
      rule: '<i class="fas fa-spinner fa-spin"></i> ...'
    }
  },
  components: {
    VueMarkdown
  },
  mounted () {
    //  GitHub 
    this.$http.get('https://api.github.com/repos/HackerSir/Articles-of-association/commits/master')
      .then(response => {
        this.ruleVersion = response.data.sha
        //  RawGit https://rawgit.com/  CDN
        let ruleUrl = 'https://cdn.rawgit.com/HackerSir/Articles-of-association/' + this.ruleVersion + '/rules.md'
        //  rules.md
        this.$http.get(ruleUrl)
          .then(response => {
            this.rule = response.data
          })
      })
  }
}
</script>



